.person {
  &__title {
    @include font(24px, 94.5%, 400);
    font-family: Playfair Display;
    text-align: center;
    margin: 32px 0 32px;
  }
  &__img {
    margin: 0 auto;
  }
  &__text {
    @include font(22px, 94.5%, 400);
    font-family: Oswald;
    margin-right: 6px;
    margin-top: 20px;
  }
  &__icon {
    width: 16px;
    height: 16px;
  }
  &__wrap {
    display: flex;
    align-items: flex-end;
    justify-content: center;
    margin-bottom: 20px;
  }
}
.person__dressCode {
  background-color: $bgColorBtn;
  &__title,
  &__text--top,
  &__text--bottom {
    color: #e4b0a2;
  }
  &__wrapper {
    border: solid 1px #e4b0a2;
  }
}
.person__present {
  &__wrapper {
    border: solid 1px #1e4d51;
  }
  &__text--right {
    @include font(14px, 94.5%, 400);
  }
}
.person__dressCode,
.person__present {
  padding-top: 15px;
  padding-bottom: 15px;
  &__title {
    @include font(23px, 94.5%, 400);
    font-family: Playfair Display;
    text-align: center;
    margin: 12px 0 20px;
  }

  &__text--top,
  &__text--bottom {
    @include font(12px, 94.5%, 400);
    font-family: Oswald;
  }
  &__content {
    display: flex;
    align-items: center;
  }
  &__wrap {
    display: flex;
    margin: 0 0 27px 4px;
  }
  &__wrap--right {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-left: 10px;
  }

  &__text--bottom {
    text-align: right;
  }
}
